.header {

  &__line {
    width: 100%;
    height: 21px;
    background: #d9d9d9;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    gap: 25px;
    margin-bottom: 10px;
  }

  &__logo {
    width: 393px;
    height: auto;
    img {
      object-fit: contain;
    }
  }

  &__address {
    width: 300px;
    padding-top: 6px;
  }

  &__item {
    display: flex;
    align-items: center;
    gap: 10px;
    img {
      width: 24px;
      height: 24px;
    }
  }

  &__address-place {
    margin-bottom: 20px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: var(--main);
  }

  &__address-time--clock {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: var(--main);
    span {
      font-weight: 600;
    }
  }

  &__contact-phone {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
    color: var(--text-1);
    margin-bottom: 10px;
  }

  &__contact-email {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: var(--main);
  }

  &__call-btn {
    margin-bottom: 14px;
  }

  &__social {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 14px;
  }
  &__contact--mobile {
    display: none;
  }
  &__burger {
    display: none;
    width: 26px;
    height: 22px;
    cursor: pointer;
    flex-shrink: 0;
  }
  &__logo-mobile {
    display: none;
  }
  &__wrapper--mobile {
    display: none;
  }
}

.social-link {
  width: 32px;
  height: 32px;
}

.menu {
  background: var(--main);
}

.nav {

  &__list {
    display: flex;
    align-items: center;
    gap: 34px;
    justify-content: center;
  }

  &__item {
    padding: 0 12px;

  }

  &__link {
    padding: 12px 0;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 12px;
    line-height: 130%;
    text-transform: uppercase;
    text-align: center;
    color: var(--white);
    position: relative;
    &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 11px;
    display: block;
    width: 100%;
    height: 1px;
    background: var(--white);
    opacity: 0.35;
  }

  }
}

.nav__item {
  &:hover .nav__link::after {
    display: none;
  }
  &:active .nav__link {
    transition: all 0.1s;
    transform: scale(1.04);
  }
}

.header__down {
  position: relative;
  &:hover .header__down-lists{
    display: flex;
  }
  &:hover .nav__link::after {
    display: block;
  }
  &:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
  }
  &:active .nav__link::after {
    display: none;
  }
  &-lists {
    padding: 0 23px;
    position: absolute;
    top: 39px;
    left: 0px;
    // display: flex;
    display: none;
    width: 913px;
    align-items: start;
    justify-content: start;
    flex-wrap: wrap;
    background: var(--main);
    border-radius: 0 0 6px 6px;
    z-index: 20;
    gap: 32px;

  }
  &-item {
    flex: 0 0 186px;
  }
  &-list {
    width: 100%;
    padding: 9px 0;
    display: flex;
    align-items: center;
    gap: 6px;
  }
  &-icon {
    width: 28px;
    height: 28px;
  }
  &-link {
    position: relative;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 14px;
    line-height: 110%;
    color: var(--white);
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0px;
      display: block;
      width: 100%;
      height: 1px;
      background: var(--white);
      opacity: 0.35;
    }
    &:hover::after {
      display: none;
    }
  }
}
@media (max-width: 1360px) {
  .header__down-lists {
    width: 460px;
    gap: 0px 40px;
  }
}
@media (max-width: 680px) {
  .header__down-lists {
    top: 36px;
  }
  .nav__item {
    padding: 0 6px;

  }
}
@media (max-width: 615px) {
  .header__down-lists {
    width: 230px;
    gap: 0px;
  }
}

.menu__fixed-medium {
  display: none;
}
.menu__fixed {
  opacity: 0;
  display: none;
  &-wrapper {
    display: flex;
    align-items: center;
    gap: 105px;
    justify-content: end;
    min-height: 40px;
  }
  &-logo {
    width: 88px;
    height: 36px;
    flex-shrink: 0;
  }
  .header__nav {
    max-width: 726px;
    width: 100%;
  }
  .nav__list {
    justify-content: space-between;
    gap: 10px;
  }
  .header__call-btn {
    border-radius: 6px;
    width: 220px;
    height: 30px;
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 16px;
    line-height: 130%;
    color: var(--white);
    margin-bottom: 0;
  }
  &.active {
    display: block;
    opacity: 1;
    position: fixed;
    width: 100%;
    transition: 1s ease;
    z-index: 100;
    top: 0;
    left: 0;
    min-height: 40px;
    padding-top: 2px;
  }
}
@media (max-width: 1240px) {
  .menu__fixed {
    .menu__fixed-wrapper{
      gap: 20px;
      justify-content: space-between;
    }
    .nav__link {
      font-size: 10px;
    }
    .nav__list {
      gap: 0px;
    }
  }
  .menu__fixed .header__nav {
    max-width: 510px;
    width: 100%;
  }
  .header__down-lists {
    top: 36px;
  }
}
@media (max-width: 950px) {
  .menu__fixed {
    display: none;
  }
  .menu__fixed.active {
    display: none;
  }
}

