      height: 30px;
      font-size: 12px;
    }
  }
}
@media (max-width: 615px) {
  .menu__fixed-medium{
    .menu__fixed-wrapper{
      margin-left: 16px;
    }
  }
}
@media (max-width: 600px) {
  .menu__fixed-medium{
    .header__call-btn{
      display: none;
    }
  }
}
@media (max-width: 550px) {
  .menu__fixed-medium{
    display: none;
  }
  .menu__fixed-medium.active{
    display: none;
  }
}


@media (max-width: 1312px) {
  .header__wrapper {
    gap: 12px;
  }
}
@media (max-width: 1280px) {

  .header__logo {
    display: none;
  }
  .header__logo-mobile {
    display: block;
    flex: 0 0 177px;
    height: auto;
    img {
      object-fit: contain;
    }

  }
  .header__wrapper {
    justify-content: space-between;
