@media (max-width: 320px) {
  .header__wrapper--mobile {

    .header__logo-wrapper {
      display: flex;
      align-items: center;
      justify-content: start;
      gap: 19px;
    }
}
}

.dl-menuwrapper--mobile {
  display: none;
}
.dl-menuwrapper li > a:not(:only-child)::after {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 6px;
  height: 9px;
  background: url("../img/polygon-right.svg") no-repeat center ;
}

.dl-menuwrapper li.dl-back:after {
  content: "";
  position: absolute;
  top: 20%;
  transform: translateY(-50%);
  width: 6px;
  height: 9px;
  background: url("../img/polygon-left.svg") no-repeat left center;
  right: auto;
  left: 0;
}
.dl-menuwrapper .dl-menu.dl-menuopen {
  padding: 21px 40px 26px 40px;
}
.dl-menuwrapper li a {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 0;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;
  color: var(--white);
}
.dl-menuwrapper li a span {
border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}
.dl-menuwrapper li:not(:last-child) {
  margin-bottom: 14px;
}
.dl-menuwrapper {
  z-index: 10;
}
.dl-menuwrapper li.dl-back > a {
  padding-left: 19px;
}
.dl-menuwrapper li.dl-back {
  position: relative;
}
.dl-menu.dl-subview li.dl-subviewopen > .dl-submenu .dl-back a::after{
  display: none;
}

.dl-menu.dl-subview li.dl-subviewopen > .dl-submenu .dl-back a{
  padding-bottom: 21px;
  border-bottom: 1px solid #fff;
  margin-bottom: 21px;
}

.dl-menu.dl-subview li.dl-subviewopen > .dl-submenu > li {
  border-radius: 0px;
}
.dl-menu__close {
  position: absolute;
  top: 0px;
  // right: -21px;
  right: 0;
  width: 18px;
  height: 18px;
  z-index: 100;
}
.dl-menu.dl-subview li.dl-subviewopen > .dl-submenu {
  position: relative;
}
.dl-menuwrapper--desctop .dl-menu.dl-menuopen .dl-menu__close.dl-close{
  display: none;
}
.dl-menuwrapper--desctop .dl-menu.dl-menuopen.dl-subview .dl-menu__close.dl-close{
  display: block;
  right: 20px;
  top: 20px;
}

@media (max-width: 680px) {
.dl-menuwrapper .dl-menu {
  top: 37px;
}
}

@media (max-width: 550px) {
  .dl-menuwrapper--desctop {
   display: none;
  }
  .dl-menuwrapper--mobile {
    display: block;
    left: 0;
  }

  .dl-menuwrapper button:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    border-radius: 50px;
    background: #4c699b;
    top: 1px;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-box-shadow: 0 10px 0 #4c699b, 0 20px 0 #4c699b;
    box-shadow: 0 10px 0 #4c699b, 0 20px 0 #4c699b;
}
.header__wrapper--mobile .header__burger {
  background: transparent;
}
.dl-menuwrapper button.dl-active{
  background: transparent;
}
.dl-menuwrapper button {
  background: transparent;
}
.dl-menuwrapper button:hover, .dl-menuwrapper button.dl-active {
  background: transparent;
}

.dl-menuwrapper button {
  width: 26px;
  height: 24px;
  top: 0px;
}
.dl-menuwrapper .dl-menu.dl-menuopen {
  border-radius: 6px;
}
.dl-menu__close {
  top: 20px;
  right: 20px;
}
.dl-menu__logo {
  width: 88px;
  height: 36px;
  margin-top: -10px;
  margin-bottom: 10px;
}
.dl-menu.dl-menuopen.dl-subview .dl-menu__logo {
  display: none;
}

.menu {

  &__mobile-content {
    border-top: 1px solid #fff;
    margin-top: 20px;
  }

  &__mobile-btn {
    border-radius: 6px;
    width: 230px;
    height: 52px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    margin-top: 20px;
    background: var(--accent);
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
    color: var(--white);
    &:hover {
      background: #ff9213;
    }
    &:active {
      background: #f00;
    }
  }

  &__mobile-phone {
    .menu__mobile-img {
      width: 20px;
      height: 20px;
      a {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 16px;
        line-height: 130%;
        color: var(--white);
      }
    }
  }

  &__mobile-address {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 12px;
    line-height: 130%;
    color: var(--white);
    img {
      width: 18px;
      height: 22px;
    }
  }

  &__mobile-email {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--white);
    img {
      width: 20px;
      height: 16px;
    }
  }

  &__mobile-item {
    display: flex;
    align-items: center;
    gap: 6.5px;

  }
  &__mobile-item:not(:last-child){
    margin-bottom: 10px;
  }
  &__mobile-item:first-child {
    margin-top: 20px;
  }
  &__mobile-social {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 14px;
    margin-top: 20px;
  }
  &__mobile-link {
    width: 32px;
    height: 32px;
  }
}


}

